let code = {}

code.base = `
// 参数是一个百分比
let percent = 50
this.$LoadingBar.run(percent)
`

code.complete = `
// 参数是个完整的对象
let params = {
  color: 'red',
  percent: 60,
  duration: 3000
}
this.$LoadingBar.run(params)
`

export default code



