let code = {}

code.string = `
<animate-text
  class="animate-text-string"
  :time="700"
  value="这里是需要动画出现的文本内容"
  @animated="function () {console.log('这里是动画结束的回调函数')}">
</animate-text>
`

code.number = `
<animate-text
  class="animate-text-number"
  :value="123456"
  :is-number="true"
  :start-number="0"
  :time="2000"
  :change-count="50"
  @animated="function () {console.log('这里是动画结束的回调函数')}">
  >
</animate-text>
`

export default code



