<template>
  <x-row class="example">
    <x-col :span="12" class="example-demo" ref="demo">
      <div class="example-case">
        <slot name="demo"></slot>
      </div>
      <header class="example-header"><span>{{ title }}</span></header>
      <div class="example-desc">
        <slot name="desc"></slot>
      </div>
    </x-col>
    <div class="example-split"></div>
    <x-col :span="12" class="example-code" :style="codeStyles" :class="{'example-code--unfold': !fold}">
      <slot name="code"></slot>
      <div class="example-code-more" @click="toggle" v-if="showCodeMore">
        <icon type="angle-down"></icon>
      </div>
    </x-col>
  </x-row>
</template>

<script>
export default {
  name: 'Example',

  props: ['title'],

  data () {
    return {
      demoHeight: 0,
      codeHeight: 0,
      fold: true,
      showCodeMore: false
    }
  },

  computed: {
    codeStyles () {
      return {
        height: this.fold ? `${this.demoHeight}px` : `${this.codeHeight}px`
      }
    }
  },

  mounted () {
    setTimeout(() => {
      this.demoHeight = this.$refs['demo'].$el.clientHeight
      this.codeHeight = this.$slots['code'].reduce((prev, next) => {
        return prev + next.elm.clientHeight
      }, 0)
      this.showCodeMore = this.codeHeight > this.demoHeight
    }, 200)
  },

  methods: {
    toggle () {
      this.fold = !this.fold
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.example
  position relative
  border-width 1px
  border-style solid
  transition all animation-time ease-in-out
  margin-bottom 20px

  &:hover
    border-color transparent

.example-demo
  position relative
  padding 20px 0 10px

.example-case
  padding 0 20px

.example-header
  margin 30px 0 10px
  position relative

  &:before
    absolute left top 50%
    content ''
    size 100% 0
    border-top-width 1px
    border-top-style solid

  span
    position relative
    display inline-block
    padding 0 18px
    margin-left 30px
    font-size 14px

.example-split
  absolute left 50% top bottom
  size 0 100%
  margin-left -1px
  border-left-width 1px
  border-left-style dashed

.example-desc
  padding 0 20px

  code
    display inline-block
    padding 0 5px
    margin 0 3px
    border-radius 2px
    border-width 1px
    border-style solid
    font-family mono

.example-code
  padding 0 10px 20px
  box-sizing border-box
  overflow hidden
  transition all transition-time

  &--unfold
    .example-code-more
      .{icon-prefix}
        transform rotate(180deg)

.example-code-more
  absolute left bottom
  size 100% 40px
  cursor pointer

  &:after
    absolute left top
    content ''
    size 100%
    transition all animation-time

  .{icon-prefix}
    absolute left 50% top 50%
    margin 5px 0 0 -8px
    transition all animation-time
    z-index 1

// 主题配色
.theme-light
  .example
    border-color #EEE
    &:hover
      box-shadow 0 2px 7px rgba(0, 0, 0, .15)
  .example-header
    &:before
      border-top-color #E3E3E3
    span
      background-color #FFF
  .example-split
    border-left-color border-color-base
  .example-desc
    color gray
    code
      background #FFF
      border-color #E3E3E3
  .example-code-more
    &:after
      box-shadow inset 0 -15px 30px #FFF
    &:hover
      &:after
        box-shadow inset 0 -25px 30px #FFF

.theme-dark
  .example
    border-color #323238
    &:hover
      box-shadow 0 2px 7px #333
  .example-header
    &:before
      border-top-color #323238
    span
      background-color #141414
  .example-split
    border-left-color #323238
  .example-desc
    color #999
    code
      background #141414
      border-color #323238
  .example-code-more
    &:after
      box-shadow inset 0 -15px 30px #2a2a2d
    &:hover
      &:after
        box-shadow inset 0 -25px 30px #2a2a2d
</style>



