<template>
  <layout>
    <div class="sec-nav" slot="secondaryNav">
      <template v-for="item in nav">
        <div class="sec-nav__title">{{ item.title }}</div>
        <ul>
          <li class="sec-nav__item" v-for="(val, key) in item.links">
            <router-link :to="{path: '/component/' + key}">{{ val }}</router-link>
          </li>
        </ul>
      </template>
    </div>
    <transition name="slide-left">
      <router-view></router-view>
    </transition>
  </layout>
</template>

<script>
import Layout from '../layout'
import nav from './data/nav'

export default {
  name: 'Docs',

  components: {
    Layout
  },

  data () {
    return {
      nav
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.sec-nav
  padding-top 30px
  box-sizing border-box

  ul
    margin-bottom 30px

.sec-nav__item
  border-bottom none
  & > a
    display block
    padding 5px 0 5px 20px
    font-size 12px

  .router-link-active
    position relative

    &:after
      content ''
      absolute right top
      size 2px 100%
      background primary-color

.sec-nav__title
  padding 10px 0 10px 20px

// 主题配色
.theme-light
  .sec-nav__item
    & > a
      color text-color
      &:hover
        background #e5e5e5
    .router-link-active
      background #e5e5e5
  .sec-nav__title
    color light-gray

.theme-dark
  .sec-nav__item
    & > a
      color #999
      &:hover
        background #2E2E2E
    .router-link-active
      background #2E2E2E
  .sec-nav__title
    color #666
</style>



