<template>
  <demo-article>
    <p>loading组件</p>
    <example title="基础用法">
      <div slot="demo">
        <x-button @click.native="showGlobalSpin">全局组件（3s后关闭）</x-button>
      </div>
      <div slot="desc">
        <p>见右侧，option是可选参数，可不传</p>
      </div>
      <div slot="code">
        <x-code lang="javascript" slot="code" v-for="type in ['start', 'stop']" :key="type">{{ code[type] }}</x-code>
      </div>
    </example>
    <example title="基础用法">
      <div slot="demo">
        <x-row>
          <x-col :span="10">
            <x-button @click.native="showLocalSpin">局部组件运行</x-button>
            <x-button @click.native="closeLocalSpin">局部组件停止</x-button>
          </x-col>
          <x-col :span="14">
            <spin :show="isLocalShow"
                  text="loading"
                  :with-mask="true"
                  color="red"
                  :size="50"
                  class="mt20">
            </spin>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>见右侧</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.local }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/spin'

export default {
  name: 'SpinDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      isLocalShow: false
    }
  },
  methods: {
    showGlobalSpin () {
      let option = { // 可选参数，可不传
        text: 'global loading',
        withMask: true,
        color: 'red',
        size: 50
      }

      this.$Spin.start(option)
      setTimeout(() => {
        this.$Spin.stop()
      }, 3000)
    },
    showLocalSpin () {
      this.isLocalShow = true
    },
    closeLocalSpin () {
      this.isLocalShow = false
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.spin-wrap {
  position relative
}
</style>



