<template>
  <demo-article>
    <p>基本组件-单选框。主要用于一组可选项单项选择，或者单独用于切换到选中状态。</p>
    <example title="单独使用">
      <div slot="demo">
        <radio v-model="single">今年发大财</radio>
        <p>{{ single }}</p>
      </div>
      <div slot="desc">
        <p>使用<code>v-model</code>可以双向绑定数据</p>
      </div>
      <x-code lang="html" slot="code">{{ code.single }}</x-code>
    </example>
    <example title="单选框组">
      <div slot="demo">
        <radio-group v-model="os">
          <radio label="iOS"></radio>
          <radio label="Android"></radio>
          <radio label="Windows"></radio>
        </radio-group>
        <p>{{ os }}</p>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.group }}</x-code>
    </example>
    <example title="垂直布局">
      <div slot="demo">
        <radio-group v-model="os" vertical>
          <radio label="iOS"></radio>
          <radio label="Android"></radio>
          <radio label="Windows"></radio>
        </radio-group>
        <p>{{ os }}</p>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.vertical }}</x-code>
    </example>
    <example title="单选按钮组">
      <div slot="demo">
        <radio-group type="button" v-model="period">
          <radio label="周"></radio>
          <radio label="月"></radio>
          <radio label="年"></radio>
        </radio-group>
        <p>{{ period }}</p>
      </div>
      <div slot="desc">
        <p>指定<code>type</code>属性为'button'可以让单选框组表现为按钮组的外观</p>
      </div>
      <x-code lang="html" slot="code">{{ code.button }}</x-code>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <div class="mb10">
          <radio-group type="button" size="small" v-model="period">
            <radio label="周"></radio>
            <radio label="月"></radio>
            <radio label="年"></radio>
          </radio-group>
        </div>
        <div class="mb10">
          <radio-group type="button" v-model="period">
            <radio label="周"></radio>
            <radio label="月"></radio>
            <radio label="年"></radio>
          </radio-group>
        </div>
        <div class="mb10">
          <radio-group type="button" size="large" v-model="period">
            <radio label="周"></radio>
            <radio label="月"></radio>
            <radio label="年"></radio>
          </radio-group>
        </div>
        <p>{{ period }}</p>
      </div>
      <div slot="desc">
        <p>指定<code>size</code>属性设置不同尺寸的按钮组，可选值为 <code>small</code> 和 <code>large</code></p>
      </div>
      <x-code lang="html" slot="code">{{ code.size.small }}</x-code>
      <x-code lang="html" slot="code">{{ code.size.normal }}</x-code>
      <x-code lang="html" slot="code">{{ code.size.large }}</x-code>
    </example>
    <example title="禁用状态">
      <div slot="demo">
        <radio-group type="button" v-model="period">
          <radio label="周" disabled></radio>
          <radio label="月" disabled></radio>
          <radio label="年" disabled></radio>
        </radio-group>
        <radio v-model="disabledSingle" disabled>排骨饭</radio>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/radio'

export default {
  name: 'RadioDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      single: false,
      disabledSingle: true,
      os: 'iOS',
      period: '周'
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



