<template>
  <demo-article>
    <p>用于显示轻量的消息</p>
    <example title="基础用法">
      <div slot="demo">
        <x-button @click.native="$Message.info('这是一条普通消息')">普通消息</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="消息类型">
      <div slot="demo">
        <x-button @click.native="$Message.success('这是一条成功消息')">成功消息</x-button>
        <x-button @click.native="$Message.warning('这是一条警告消息')">警告消息</x-button>
        <x-button @click.native="$Message.error('这是一条错误消息')">错误消息</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.type }}</x-code>
    </example>
    <example title="延时关闭">
      <div slot="demo">
        <x-button @click.native="$Message.info('5秒后我会自动关闭', 5)">5秒后关闭</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.delay }}</x-code>
    </example>
    <example title="关闭后回调">
      <div slot="demo">
        <p>3秒后关闭并显示成功消息</p>
        <x-button @click.native="$Message.info('3秒后显示回调信息', 3, showSuccess)">普通消息</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.callback }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/message'

export default {
  name: 'MessageDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  },

  methods: {
    showSuccess () {
      this.$Message.success('我是回调信息')
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



