<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <x-button size="small" @click.native="showModal = true">显示modal</x-button>

        <modal
          :show="showModal"
          @close="showModal=false"
          >
          <x-select v-model="os" size="small">
            <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
          </x-select>
        </modal>

      </div>
      <div slot="desc">
        <p>模态窗组件, show属性必传</p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/modal'

export default {
  name: 'ModalDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      showModal: false,
      os: '',
      osList: [{
        value: 'windows',
        label: 'Windows',
        disabled: false
      }, {
        value: 'android',
        label: 'Android',
        disabled: true
      }, {
        value: 'linux',
        label: 'Linux',
        disabled: false
      }, {
        value: 'mac',
        label: 'Mac OS',
        disabled: false
      }]
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



