<template>
  <demo-article>
    <p>拖放组件介绍</p>
    <example title="基础用法">
      <div slot="demo">
        <h3>效果展示</h3>
        <div class="img">
          <img src="https://raw.githubusercontent.com/qgh810/draggable/master/demo/demo.gif" alt="" width="400">
        </div>
        <h3>可以拖动下方标签尝试使用</h3>
        <draggable
          class="drag-box"
          >
          拖动我
        </draggable>
        <droppable
          class="drop-box"
          @dragstart="onDragStart"
          @dragend="onDragEnd"
          @drop="onDrop"
          @dragover="onDragOver"
          @dragenter="onDragEnter"
          @dragleave="onDragLeave">

          <div class="placeholder">被拖动的东西可以在这里放下</div>

        </droppable>
      </div>
      <div slot="desc">
        <p>
          拖放组件, 拖和放各为一个组件, 可以相互嵌套,拷贝右侧代码即可使用.<br>
          跟随鼠标移动的小图标可以自定义, 内置四种常用图标.<br>
          具体用法查看使用说明文档
          <a @click="open('https://github.com/qgh810/dnd')">https://github.com/qgh810/dnd</a>
        </p>
      </div>
      <x-code lang="html" slot="code">{{ code.drag }}</x-code>
      <x-code lang="html" slot="code">{{ code.drop }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/dnd'

export default {
  name: 'DndDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  },

  methods: {
    open (url) {
      window.open(url)
    },
    onDragStart (params) {
      params.el.style.background = 'rgba(0,0,255,0.1)'
    },
    onDragEnd (params) {
      !params.enter && params.methods.removeDragedNode('back')
      params.el.style.background = 'none'
    },
    onDrop (params) {
      params.el.appendChild(params.sourceNode.cloneNode(true))
      params.methods.removeDragedNode('fade')
    },
    onDragEnter (params) {
      params.methods.showStateIcon('https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1490944919248&di=e493345787b16778a595ebfb4d9d3857&imgtype=0&src=http%3A%2F%2Fimg34.nipic.com%2F20120530%2F5312252_142725085317_1.jpg')
      params.el.style.background = 'rgba(0,0,255,0.15)'
    },
    onDragLeave (params) {
      params.methods.hideStateIcon()
      params.el.style.background = 'rgba(0,0,255,0.1)'
    },
    onDragOver (params) {
      console.log(params)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.img
  max-width 400px

  img
    display block
    width 100%

.drag-box
  position relative
  display inline-block
  margin 10px
  padding 5px 10px
  height 30px
  box-sizing border-box
  background orange
  color #fff
  &:after
    content ''
    position absolute
    left 100%
    top 0
    display block
    width 0
    height 0
    border-top 15px solid transparent
    border-left 5px solid orange
    border-bottom 15px solid transparent

.drop-box
  position relative
  margin-right 10px
  width 200px
  height 200px
  border 1px solid #999
  overflow auto
  .placeholder
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    line-height 200px
    text-align center
    color #ccc
</style>



