<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <x-select v-model="os" style="width: 200px;">
          <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
        </x-select>
      </div>
      <div slot="desc">
        <p>基本用法。可以使用 <code>v-model</code> 双向绑定数据。</p>
        <p>单选时，<code>value</code> 只接受字符串和数字类型，多选时，只接受数组类型，组件会自动根据 <code>&lt;x-option&gt;</code> 的 <code>value</code>来返回选中的数据。</p>
        <p>可以给 <code>&lt;x-select&gt;</code>添加 <code>style</code> 样式，比如宽度。</p>
        <p>在展开选择器后，可以使用键盘的 <code>up</code> 和 <code>down</code> 快速上下选择，按下Enter选择，按下Esc收起选择器。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
        <x-code lang="js" slot="code">{{ code.baseData }}</x-code>
      </div>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <x-row>
          <x-col :span="8">
            <x-select v-model="os" size="small">
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
          <x-col :span="8">
            <x-select v-model="os">
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
          <x-col :span="8">
            <x-select v-model="os" size="large">
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置 <code>size</code> 属性为组件指定尺寸，可选值为 <code>small</code>、<code>large</code></p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.size }}</x-code>
      </div>
    </example>
    <example title="禁用">
      <div slot="demo">
        <div>
          <x-select v-model="os" style="width: 200px;" disabled>
            <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
          </x-select>
        </div>
        <div>
          <x-select v-model="os" style="width: 200px;">
            <x-option v-for="item in osList" :value="item.value" :key="item" :disabled="item.disabled">{{ item.label }}</x-option>
          </x-select>
        </div>
      </div>
      <div slot="desc">
        <p>sasd</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
      </div>
    </example>
    <example title="可清空">
      <div slot="demo">
        <x-select v-model="os" style="width: 200px;" clearable>
          <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
        </x-select>
      </div>
      <div slot="desc">
        <p>这里是用法说明。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.clearable }}</x-code>
      </div>
    </example>
    <example title="多选">
      <div slot="demo">
        <div class="mb10">{{ osArr | json}}</div>
        <x-select v-model="osArr" style="width: 200px;" multiple>
          <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
        </x-select>
      </div>
      <div slot="desc">
        <p>这里是用法说明。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.multiple }}</x-code>
        <x-code lang="js" slot="code">{{ code.multipleData }}</x-code>
      </div>
    </example>
    <example title="可搜索">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <x-select v-model="os" style="max-width: 200px;" filterable>
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
          <x-col :span="12">
            <x-select v-model="osArr" style="max-width: 200px;" multiple filterable>
              <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
            </x-select>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>这里是用法说明。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.filterable }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/select'

export default {
  name: 'SelectDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      os: '',
      osArr: [],
      osList: [{
        value: 'windows',
        label: 'Windows',
        disabled: false
      }, {
        value: 'android',
        label: 'Android',
        disabled: true
      }, {
        value: 'linux',
        label: 'Linux',
        disabled: false
      }, {
        value: 'mac',
        label: 'Mac OS',
        disabled: false
      }]
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



