<template>
  <demo-article>
    <p>基础组件，触发业务逻辑时使用。</p>
    <example title="按钮类型">
      <div slot="demo">
        <x-button>Default</x-button>
        <x-button type="primary">Primary</x-button>
        <x-button type="ghost">Ghost</x-button>
        <x-button type="dashed">Dashed</x-button>
        <x-button type="text">Text</x-button>
        <x-button type="success">Success</x-button>
        <x-button type="warning">Warning</x-button>
        <x-button type="error">error</x-button>
      </div>
      <div slot="desc">
        <p>按钮类型有：默认按钮、主按钮、幽灵按钮、虚线按钮以及几种业务逻辑按钮</p>
        <p>通过设置 <code>type</code> 为 <code>primary</code>、<code>ghost</code>、<code>dashed</code>、<code>text</code>、<code>success</code>、<code>warning</code>、<code>error</code> 创建不同样式的按钮，不设置为默认样式。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.type }}</x-code>
    </example>
    <example title="按钮尺寸">
      <div slot="demo">
        <x-button size="mini">Mini</x-button>
        <x-button size="small">Small</x-button>
        <x-button>Normal</x-button>
        <x-button size="large">Large</x-button>
        <x-button size="xlarge">Xlarge</x-button>
      </div>
      <div slot="desc">
        <p>通过设置 <code>mini</code>、<code>small</code>、<code>large</code> 和 <code>xlarge</code> 创建不同尺寸的按钮，不设置为默认尺寸。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.size }}</x-code>
    </example>
    <example title="禁用状态">
      <div slot="demo">
        <div class="mb10">
          <x-button>Normal</x-button>
          <x-button disabled>Normal(Disabled)</x-button>
        </div>
        <div class="mb10">
          <x-button type="primary">Primay</x-button>
          <x-button type="primary" disabled>Primay(Disabled)</x-button>
        </div>
        <div class="mb10">
          <x-button type="dashed">Dashed</x-button>
          <x-button type="dashed" disabled>Dashed(Disabled)</x-button>
        </div>
      </div>
      <div slot="desc">
        <p>设置 <code>disabled</code> 以禁用按钮。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
    </example>
    <example title="按钮组">
      <div slot="demo">
        <div class="mb10">
          <button-group size="mini">
            <x-button>Prev</x-button>
            <x-button>Next</x-button>
          </button-group>
        </div>
        <div class="mb10">
          <button-group size="small">
            <x-button>Prev</x-button>
            <x-button disabled>Next</x-button>
          </button-group>
        </div>
        <div class="mb10">
          <button-group>
            <x-button>Prev</x-button>
            <x-button>Next</x-button>
          </button-group>
        </div>
        <div class="mb10">
          <button-group size="large">
            <x-button>Prev</x-button>
            <x-button>Next</x-button>
          </button-group>
        </div>
        <div class="mb10">
          <button-group size="xlarge">
            <x-button>Prev</x-button>
            <x-button>Next</x-button>
          </button-group>
        </div>
      </div>
      <div slot="desc">
        <p>将 <code>&lt;x-button&gt;</code> 组件嵌套于 <code>&lt;button-group&gt;</code> 组件中以创建按钮组，<code>&lt;x-button&gt;</code> 组件的各种属性仍然适用。</p>
        <p>设置 <code>size</code> 属性创建不同尺寸的按钮组，不设置为默认尺寸。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.btnGroup }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/button'

export default {
  name: 'ButtonDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



