<template>
  <demo-article>
    <p>用于鼠标滑过提示信息</p>
    <example title="摆放位置">
      <div slot="demo">
        <div class="demo-tooltip">
          <div class="top">
            <tooltip content="我是提示文案" placement="top-start">
              <x-button>上左</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="top">
              <x-button>上中</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="top-end">
              <x-button>上右</x-button>
            </tooltip>
          </div>
          <div class="left">
            <tooltip content="我是提示文案" placement="left-start" class="mb5">
              <x-button>左上</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="left" class="mb5">
              <x-button>左中</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="left-end">
              <x-button>左下</x-button>
            </tooltip>
          </div>
          <div class="right">
            <tooltip content="我是提示文案" placement="right-start" class="mb5">
              <x-button>右上</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="right" class="mb5">
              <x-button>右中</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="right-end">
              <x-button>右下</x-button>
            </tooltip>
          </div>
          <div class="bottom">
            <tooltip content="我是提示文案" placement="bottom-start">
              <x-button>下左</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="bottom">
              <x-button>下中</x-button>
            </tooltip>
            <tooltip content="我是提示文案" placement="bottom-end">
              <x-button>下右</x-button>
            </tooltip>
          </div>
        </div>
      </div>
      <div slot="desc">
        <p>提供 <code>top-start</code>、<code>top</code>、<code>top-end</code>、<code>left-start</code>、<code>left</code>、<code>left-end</code>、<code>bottom-start</code>、<code>bottom</code>、<code>bottom-end</code>、<code>right-start</code>、<code>right</code>、<code>right-end</code> 共12个摆放位置</p>
      </div>
      <x-code lang="html" slot="code">{{ code.placement }}</x-code>
    </example>
    <example title="延时">
      <div slot="demo">
        <tooltip content="我是提示文案" :delay="1000">
          <x-button>延时1秒</x-button>
        </tooltip>
      </div>
      <div slot="desc">
        <p>设置 <code>delay</code> 属性延时显示提示，单位：毫秒</p>
      </div>
      <x-code lang="html" slot="code">{{ code.delay }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/tooltip'

export default {
  name: 'TooltipDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      visible: false
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.demo-tooltip
  max-width 400px

  .top
    text-align center

  .left
    float left
    width 60px

  .right
    float right
    width 60px

  .bottom
    text-align center
    clear both
</style>



