let code = {}

code.base = `
// 不可选一半
<cascader style="width: 200px;" :data="spots" :value="spot" @change="onCascaderChange"></cascader>
`

code.baseData = `
// 不可选一半
data () {
  return {
    spot: [],
    spotSync: [{
      value: 'beijing',
      label: '北京'
    }, {
      value: 'tiantan',
      label: '天坛'
    }],
    spots: [{
      value: 'beijing',
      label: '北京',
      children: [{
        value: 'gugong',
        label: '故宫'
      }, {
        value: 'tiantan',
        label: '天坛'
      }, {
        value: 'wangfujing',
        label: '王府井'
      }]
    }, {
      value: 'jiangsu',
      label: '江苏',
      children: [{
        value: 'nanjing',
        label: '南京',
        children: [{
          value: 'fuzimiao',
          label: '夫子庙'
        }]
      }, {
        value: 'suzhou',
        label: '苏州',
        children: [{
          value: 'zhuozhengyuan',
          label: '拙政园'
        }, {
          value: 'shizilin',
          label: '狮子林'
        }]
      }]
    }]
  }
}

methods: {
  onCascaderChange (value) {
    console.log(value)
    this.spot = value
  }
}
`

code.sync = `
// 可选一半
<cascader style="width: 200px;" :data="spots" v-model="spotSync"></cascader>
`

code.syncData = `
// 可选一半
data () {
  return {
    spotSync: []
  }
}
`

code.small = `
<cascader style="width: 200px;" :data="spots" size="small" v-model="spotSmall"></cascader>
`
code.normal = `
<cascader style="width: 200px;" :data="spots" v-model="spotNormal"></cascader>
`
code.large = `
<cascader style="width: 200px;" :data="spots" size="large" v-model="spotLarge"></cascader>
`
export default code



