<template>
  <div :class="CLASS_PREFIX + '-code'">
    <pre><code :class="language" ref="code"><slot></slot></code></pre>
    <span class="copy" @click="clip">
      <icon type="clipboard" :size="14" v-if="!copied"></icon>
      <icon type="check" :size="14" color="#090" v-if="copied"></icon>
    </span>
  </div>
</template>

<script>
import 'highlight.js/styles/atom-one-dark.css'
import hljs from 'highlight.js/lib/highlight'
import Clipboard from 'clipboard'
import { CLASS_PREFIX } from '@/config'
hljs.registerLanguage('css', require('highlight.js/lib/languages/css'))
hljs.registerLanguage('javascript', require('highlight.js/lib/languages/javascript'))
hljs.registerLanguage('xml', require('highlight.js/lib/languages/xml'))

export default {
  name: 'XCode',

  props: {
    lang: {
      type: String,
      default: 'javascript'
    }
  },

  data () {
    return {
      CLASS_PREFIX,
      code: '',
      copied: false
    }
  },

  computed: {
    // 语言
    language () {
      return this.lang === 'auto' ? '' : this.lang
    }
  },

  mounted () {
    this.code = this.$refs.code.innerHTML.replace(/\n/, '')
    this.$refs.code.innerHTML = this.code
    hljs.highlightBlock(this.$refs.code)
  },

  methods: {
    /**
     * 复制到剪贴板
     * @return {String}
     */
    clip () {
      const code = this.code.replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&')
      let clipboard = new Clipboard('.copy', {
        text () {
          return code
        }
      })

      clipboard.on('success', (e) => {
        e.clearSelection()
        clipboard.destroy()
        this.copied = true
        this.$Message.success('代码已复制到剪贴板')
        setTimeout(() => {
          this.copied = false
        }, 2000)
      })
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.{css-prefix}-code
  position relative

  .copy
    absolute top 2px right
    padding 2px 5px
    cursor pointer
    border-radius 0 0 3px 3px
    transition color transition-time

  .hljs
    padding 15px
    background none

// 主题配色
.theme-light
  .{css-prefix}-code
    .copy
      color extra-light-gray
      &:hover
        color light-gray
.theme-dark
  .{css-prefix}-code
    .copy
      color #4A4A4A
      &:hover
        color #666
</style>



