<template>
  <demo-article>
    <p>用于显示系统通知消息</p>
    <example title="基础用法">
      <div slot="demo">
        <p>仅标题</p>
        <x-button @click.native="open(false)">显示通知</x-button>
        <p>带描述</p>
        <x-button @click.native="open(true)">显示通知</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="带图标">
      <div slot="demo">
        <p>不带描述</p>
        <x-button @click.native="showInfo(false)">普通</x-button>
        <x-button @click.native="showSuccess(false)">成功</x-button>
        <x-button @click.native="showWarning(false)">警告</x-button>
        <x-button @click.native="showError(false)">错误</x-button>
        <p>带描述</p>
        <x-button @click.native="showInfo(true)">普通</x-button>
        <x-button @click.native="showSuccess(true)">成功</x-button>
        <x-button @click.native="showWarning(true)">警告</x-button>
        <x-button @click.native="showError(true)">错误</x-button>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="手动关闭">
      <div slot="demo">
        <x-button @click.native="showManual">通知</x-button>
      </div>
      <div slot="desc">
        <p>指定<code>duration</code>为0可以实现通知不自动关闭</p>
      </div>
      <x-code lang="html" slot="code">{{ code.manual }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/notice'

export default {
  name: 'NoticeDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  },

  methods: {
    open (withDesc) {
      this.$Notice.open({
        title: '这是标题',
        desc: withDesc ? '标题言简意赅，例如"删除成功"，更详细的内容可以放在描述信息里' : ''
      })
    },

    showInfo (withDesc) {
      this.$Notice.info({
        title: '这是标题',
        desc: withDesc ? '标题言简意赅，例如"删除成功"，更详细的内容可以放在描述信息里' : ''
      })
    },

    showSuccess (withDesc) {
      this.$Notice.success({
        title: '这是标题',
        desc: withDesc ? '标题言简意赅，例如"删除成功"，更详细的内容可以放在描述信息里' : ''
      })
    },

    showWarning (withDesc) {
      this.$Notice.warning({
        title: '这是标题',
        desc: withDesc ? '标题言简意赅，例如"删除成功"，更详细的内容可以放在描述信息里' : ''
      })
    },

    showError (withDesc) {
      this.$Notice.error({
        title: '这是标题',
        desc: withDesc ? '标题言简意赅，例如"删除成功"，更详细的内容可以放在描述信息里' : ''
      })
    },

    showManual () {
      this.$Notice.error({
        title: '永不消失的通知',
        desc: '这条通知不会自动关闭，除非你手动关闭它',
        duration: 0
      })
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



