let code = {}

code.base = `
<scroll-view class="scroll-view" >
  <div class="scroll-content"></div>
</scroll-view>
`
code.details = `
<scroll-view
  class="scroll-view"
  :suppress-scroll-x="false"
  :suppress-scroll-y="false"
  :wheel-speed="2"
  :wheel-propagation="false"
  :min-scrollbar-length="20"
  :scroll-top="0"
  :scroll-left="0"
  @scroll-y="onScrollY"
  @scroll-x="onScrollX"
  @scroll-up="onScrollUp"
  @scroll-down="onScrollDown"
  @scroll-left="onScrollLeft"
  @scroll-right="onScrollRight"
  @y-reach-start="onYReachStart"
  @y-reach-end="onYReachStart"
  @x-reach-start="onXReachStart"
  @x-reach-end="onReachEnd"
  >
  <div class="scroll-content"></div>
</scroll-view>
`

export default code



