<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <animate-text
          class="animate-text-string"
          :time="700"
          :value="string">
        </animate-text>
        <animate-text
          class="animate-text-number"
          :value="number"
          :is-number="true"
          :start-number="0"
          :time="2000"
          :change-count="50"
          >
        </animate-text>
      </div>
      <div slot="desc">
        <p>动画文本组件</p>
        <p>可以以打字机形式播放文本, 或者以数字跳变形式播放数字</p>
        <p>对应的参数设置请看右边示例代码, 更具体用法请查看组件内的备注信息</p>
      </div>
      <x-code lang="html" slot="code">{{ code.string }}</x-code>
      <x-code lang="html" slot="code">{{ code.number }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/animate-text'

export default {
  name: 'AnimateTexts',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      string: '动画文本<br> 可以自定义动画时长,<br> 会保留原有文字样式',
      number: 6666666.666
    }
  },

  mounted () {
    setTimeout(() => {
      this.string = '动画文本<br> 可以自定义动画时长,<br> 会保留原有文字样式, <br> 配合文字左右对齐方式等样式会有不一样的效果!<br>当文字或者数字改变的时候会自动重新播放动画'
    }, 3000)
    setTimeout(() => {
      this.number = -12345.678
    }, 5000)
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.animate-text-string
  width 100%
  height 100px
.animate-text-number
  width 100%
  height 40px
  margin-top 20px
</style>



