<template>
  <demo-article>
    <p>本组件使用的图标字体是<a href="http://fontawesome.io/" target="_blank">Font Awesome</a>的子集。</p>
    <example title="基础用法">
      <div slot="demo">
        <icon type="apple" :size="24" color="#666"></icon>
        <icon type="windows" :size="24" color="#009BFF"></icon>
        <icon type="android" :size="24" color="#0FAB37"></icon>
      </div>
      <div slot="desc">
        <p>你只需要使用 <code>&lt;icon&gt;</code>标签并指定合法的<code>type</code>属性，图标就能正常显示。<p>
        <p>你还可以指定它们的尺寸：<code>size</code>和颜色：<code>color</code>。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <h2 class="sub-title tac">图标示例</h2>
    <x-row class="mb30">
      <x-col :span="12" :offset="6">
        <x-input v-model="key" size="large" icon="search" placeholder="图标名称，例如：apple"></x-input>
      </x-col>
    </x-row>
    <transition name="slide-up">
      <x-row v-if="!filteredIcons.length">
        <x-col :span="12" :offset="6">
          <alert show-icon type="error">
            非常抱歉
            <template slot="desc">没找到与"{{ key }}"相关任何图标</template>
          </alert>
        </x-col>
      </x-row>
      <x-row clsss="icons" v-if="showIcons && filteredIcons.length">
        <x-col v-for="item in filteredIcons" :key="item.properties.id" :span="4">
          <div class="icons__item tac" @click="clip(item.icon.tags[0])">
            <icon :type="item.icon.tags[0]" :size="24"></icon>
            <p>{{ item.icon.tags[0] }}</p>
          </div>
        </x-col>
      </x-row>
    </transition>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import iconPack from '@/components/icon/font-awesome/selection.json'
import Clipboard from 'clipboard'
import code from './code/icon'

export default {
  name: 'IconDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      key: '',
      showIcons: false,
      icons: iconPack.icons
    }
  },

  computed: {
    filteredIcons () {
      return this.icons.filter((item) => {
        let exists = false
        item.icon.tags.forEach((tag) => {
          if (tag.indexOf(this.key) >= 0) {
            exists = true
          }
        })
        return exists
      })
    }
  },

  mounted () {
    setTimeout(() => {
      this.showIcons = true
    }, 200)
  },

  methods: {
    /**
     * 复制到剪贴板
     * @param  {String} type 图标类型
     * @return {String}
     */
    clip (type) {
      let clipboard = new Clipboard('.icons__item', {
        text () {
          return `<icon type="${type}"></icon>`
        }
      })

      clipboard.on('success', (e) => {
        e.clearSelection()
        clipboard.destroy()
        this.$Message.success('代码已复制到剪贴板')
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.icons
  transition all animation-time

.icons__item
  margin-bottom 10px
  padding 10px
  cursor pointer
  border-radius border-radius-large
  transition background animation-time ease-in-out

  p
    height 40px
    margin 10px 0 0

// 主题配色
.theme-light
  .icons__item
    &:hover
      background #EEE
.theme-dark
  .icons__item
    &:hover
      background #2B2B2F
</style>



