<template>
  <demo-article>
    <p>用于接收用户录入的文本数据</p>
    <example title="基础用法">
      <div slot="demo">
        <x-input placeholder="请输入" v-model="val1"></x-input>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <x-row>
          <x-col :span="8">
            <div class="mr5">
              <x-input size="small" placeholder="小尺寸" v-model="val2"></x-input>
            </div>
          </x-col>
          <x-col :span="8">
            <div class="mr5">
              <x-input placeholder="正常尺寸" v-model="val2"></x-input>
            </div>
          </x-col>
          <x-col :span="8">
            <x-input size="large" placeholder="大尺寸" v-model="val3"></x-input>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.size }}</x-code>
    </example>
    <example title="禁用状态">
      <div slot="demo">
        <x-input placeholder="禁用状态" disabled v-model="val4"></x-input>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
    </example>
    <example title="带图标的文本框">
      <div slot="demo">
        <x-row>
          <x-col :span="8">
            <div class="mr5">
              <x-input icon="clock-o" size="small" placeholder="小尺寸" v-model="val5"></x-input>
            </div>
          </x-col>
          <x-col :span="8">
            <div class="mr5">
              <x-input icon="clock-o" placeholder="正常尺寸" v-model="val6"></x-input>
            </div>
          </x-col>
          <x-col :span="8">
            <x-input icon="clock-o" size="large" placeholder="大尺寸" v-model="val7"></x-input>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="复合型输入框">
      <div slot="demo">
        <x-input class="mb20" v-model="val8">
          <span slot="prepend">http://</span>
          <span slot="append">.com</span>
        </x-input>
        <x-input v-model="val9">
          <x-button slot="append" @click.native="$Message.info('点击了搜索按钮')"><icon type="search"></icon></x-button>
        </x-input>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.combination }}</x-code>
    </example>
    <example title="多行文本框">
      <div slot="demo">
        <x-input type="textarea" :rows="4" v-model="val10"></x-input>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.textarea}}</x-code>
    </example>
    <example title="自适应内容高度">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <div class="mr5">
              <x-input type="textarea" v-model="val11" autosize></x-input>
            </div>
          </x-col>
          <x-col :span="12">
            <x-input type="textarea" v-model="val12" :autosize="{minRows: 2, maxRows: 5}"></x-input>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.autosize}}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/input'

export default {
  name: 'InputDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      val1: '',
      val2: '',
      val3: '',
      val4: '',
      val5: '',
      val6: '',
      val7: '',
      val8: '',
      val9: '',
      val10: '',
      val11: '',
      val12: ''
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



