<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <div>不可选一半</div>
        <cascader style="width: 200px;" :data="spots" :value="spot" @change="onCascaderChange"></cascader>
        <div>可选一半</div>
        <cascader style="width: 200px;" :data="spots" v-model="spotSync"></cascader>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
        <x-code lang="js" slot="code">{{ code.baseData }}</x-code>
        <x-code lang="html" slot="code">{{ code.sync }}</x-code>
        <x-code lang="js" slot="code">{{ code.syncData }}</x-code>
      </div>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <cascader style="width: 200px;" :data="spots" size="small" v-model="spotSmall"></cascader>
        <cascader style="width: 200px;" :data="spots" v-model="spotNormal"></cascader>
        <cascader style="width: 200px;" :data="spots" size="large" v-model="spotLarge"></cascader>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.small }}</x-code>
        <x-code lang="html" slot="code">{{ code.normal }}</x-code>
        <x-code lang="html" slot="code">{{ code.large }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/cascader'

export default {
  name: 'CascaderDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      spotSmall: [],
      spotNormal: [],
      spotLarge: [],
      spot: [],
      spotSync: [],
      spots: [{
        value: 'beijing',
        label: '北京',
        children: [{
          value: 'gugong',
          label: '故宫'
        }, {
          value: 'tiantan',
          label: '天坛'
        }, {
          value: 'wangfujing',
          label: '王府井'
        }]
      }, {
        value: 'jiangsu',
        label: '江苏',
        children: [{
          value: 'nanjing',
          label: '南京',
          children: [{
            value: 'fuzimiao',
            label: '夫子庙'
          }]
        }, {
          value: 'suzhou',
          label: '苏州',
          children: [{
            value: 'zhuozhengyuan',
            label: '拙政园'
          }, {
            value: 'shizilin',
            label: '狮子林'
          }]
        }]
      }]
    }
  },

  methods: {
    onCascaderChange (value) {
      this.spot = value
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



