<template>
  <div class="wrap">
    <header class="header">
      <div class="logo">
        <slot name="logo">
          <h1>组件库 <sup>v1.0</sup></h1>
        </slot>
      </div>
      <div class="extra-nav">
        <div class="theme-switcher">
          <radio-group type="button" size="small" v-model="theme">
            <radio label="light">
              <icon type="sun-o"></icon>
            </radio>
            <radio label="dark">
              <icon type="moon-o"></icon>
            </radio>
          </radio-group>
        </div>
      </div>
    </header>
    <section class="sidebar">
      <scroll-view class="sidebar-inner">
        <slot name="secondaryNav"></slot>
      </scroll-view>
    </section>
    <scroll-view class="main-content" ref="scrollView">
      <slot></slot>
    </scroll-view>
  </div>
</template>

<script>
export default {
  data () {
    return {
      theme: 'light'
    }
  },

  watch: {
    theme (val) {
      this.$switchTheme(val)
    }
  },

  mounted () {}
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.wrap
  height 100%
  padding-top 50px
  box-sizing border-box
  position relative

.header
  absolute left top
  size 100% 50px
  border-bottom-width 1px
  border-bottom-style solid
  box-sizing border-box
  padding-left 20px
  z-index 3

  .logo
    absolute left top
    size 220px 50px
    background-color red
    color #FFF
    line-height 50px

  .extra-nav
    float right

  .theme-switcher
    margin 11px 20px 0 0

  h1
    margin 0
    padding-left 20px
    font-size 18px
    font-weight normal

  sup
    font-size 10px
    padding 2px
    border-radius 2px

.sidebar
  absolute left top bottom
  padding-top 50px
  size 220px 100%
  box-sizing border-box
  overflow-x hidden
  overflow-y auto
  user-select none
  z-index 2

.sidebar-inner
  position relative
  z-index 1
  height 100%

.main-content
  absolute left 220px top right
  height 100%
  padding-top 50px
  box-sizing border-box

// 主题配色
.theme-light
  .header
    background #FAFAFA
    border-bottom-color #E5E5E5
  .sidebar
    background #F2F2F2
    box-shadow inset -1px 0 border-color-light
.theme-dark
  .header
    background #292929
    border-bottom none
    /*border-bottom-color #252525*/
  .sidebar
    background #212121
    box-shadow none
    /*box-shadow inset -1px 0 #252525*/
</style>



