let code = {}

code.base = `
<x-select v-model="os" style="width: 200px;">
  <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
</x-select>
`

code.baseData = `
data () {
  return {
    os: '',
    osList: [{
      value: 'windows',
      label: 'Windows',
      disabled: false
    }, {
      value: 'android',
      label: 'Android',
      disabled: true
    }, {
      value: 'linux',
      label: 'Linux',
      disabled: false
    }, {
      value: 'mac',
      label: 'Mac OS',
      disabled: false
    }]
  }
}
`

code.size = `
<x-select v-model="os" size="small">
  <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
</x-select>
`

code.disabled = `
<x-select v-model="os" style="width: 200px;" disabled>
  <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
</x-select>
`

code.clearable = `
<x-select v-model="os" style="width: 200px;" clearable>
  <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
</x-select>
`

code.multiple = `
<x-select v-model="osArr" style="width: 200px;" multiple>
  <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
</x-select>
`
code.multipleData = `
data () {
  return {
    osArr: []
  }
}
`

code.filterable = `
<x-select v-model="os" style="max-width: 200px;" filterable>
  <x-option v-for="item in osList" :value="item.value" :key="item">{{ item.label }}</x-option>
</x-select>
`

export default code



