<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <div class="progress-box">
          <x-progress :percent="percent" :status="'uploading'"></x-progress>
        </div>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'

export default {
  name: 'ProgressDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      percent: 30
    }
  },

  mounted () {
    setTimeout(() => {
      this.percent = 60
    }, 1000)
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.progress-box
  width 200px
  height 6px
  line-height 0
</style>



