<template>
  <demo-article>
    <p>取代原生的 <code>window.confirm</code>，用于暂时中断流程，待用户确认后继续的场景。</p>
    <example title="基础用法">
      <div slot="demo">
        <p v-for="type in confirmTypes">
          <x-button @click.native="showConfitm(type)">显示confirm弹窗({{type}})</x-button>
        </p>
      </div>
      <div slot="desc">
        <p><code>$Confirm</code>的方法都返回一个 <code>promise</code>，推荐按照右边代码示例用 <code>async</code> 函数调用</p>
      </div>
      <div slot="code">
        <x-code lang="javascript" slot="code" v-for="type in ['info', 'warning', 'error', 'success', 'base', 'promise', 'async']" :key="type">{{ code[type] }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/confirm'

export default {
  name: 'confirmDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      confirmTypes: ['info', 'warning', 'error', 'success']
    }
  },

  methods: {
    async showConfitm (type) {
      if (await this.$Confirm[type]({
        title: '提示',
        content: '提示的内容, 提示的内容, 提示的内容'
      })) {
        console.log('确定后的逻辑')
      } else {
        console.log('取消后的逻辑')
      }
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



