<template>
  <demo-article>
    <p>适用于状态切换的场景</p>
    <example title="基础用法">
      <div slot="demo">
        <x-switch v-model="s1" @change="$Message.info('当前变量值为' + s1)"></x-switch>
      </div>
      <div slot="desc">
        <p>使用 <code>v-mode</code> 可以为组件绑定变量。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
      </div>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <div class="mb10">
          <x-switch size="small"></x-switch>
          自动从 App Store 下载更新
        </div>
        <div class="mb10">
          <x-switch :width="60"><span slot="on">ON</span><span slot="off">OFF</span></x-switch>
        </div>
      </div>
      <div slot="desc">
        <p>设置 <code>size</code> 属性可以调整开关的尺寸。</p>
        <p>当需要显示“开”和“关”状态文案时，可以指定 <code>on</code> 和 <code>off</code> 插槽并调整 <code>width</code> 属性设置合适的宽度。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.size }}</x-code>
        <x-code lang="html" slot="code">{{ code.width }}</x-code>
      </div>
    </example>
    <example title="类型">
      <div slot="demo">
        <x-switch type="success" :value="true"></x-switch>
      </div>
      <div slot="desc">
        <p>设置 <code>type</code> 属性可以调整开关的类型。可选类型为 <code>primary</code> 和 <code>success</code>。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.type }}</x-code>
      </div>
    </example>
    <example title="禁用状态">
      <div slot="demo">
        <x-switch disabled></x-switch>
        <x-switch disabled :value="true"></x-switch>
        <x-switch type="success" disabled :value="true"></x-switch>
      </div>
      <div slot="desc">
        <p>设置 <code>disabled</code> 属性可以禁用组件。</p>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/switch'

export default {
  name: 'SwitchDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      s1: false
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



