<template>
  <demo-article>
    <p>基本组件-复选框。主要用于一组可选项多项选择，或者单独用于标记切换某种状态。</p>
    <example title="单独使用">
      <div slot="demo">
        <checkbox v-model="single">今年发大财</checkbox>
        <p>{{ single }}</p>
      </div>
      <div slot="desc">
        <p>使用<code>v-model</code>可以双向绑定数据</p>
      </div>
      <x-code lang="html" slot="code">{{ code.single }}</x-code>
    </example>
    <example title="复选框组">
      <div slot="demo">
        <checkbox-group v-model="os" @change="onOSChange">
          <checkbox label="iOS"></checkbox>
          <checkbox label="Android"></checkbox>
          <checkbox label="Windows"></checkbox>
        </checkbox-group>
        <p>{{ os | json }}</p>
      </div>
      <div slot="desc">
        <p>在 <code>&lt;checkbox-group&gt;</code> 组件中嵌套 <code>&lt;checkbox&gt;</code> 组件实现复选框组</p>
      </div>
      <x-code lang="html" slot="code">{{ code.group }}</x-code>
    </example>
    <example title="垂直布局">
      <div slot="demo">
        <checkbox-group v-model="os" @change="onOSChange" vertical>
          <checkbox label="iOS"></checkbox>
          <checkbox label="Android"></checkbox>
          <checkbox label="Windows"></checkbox>
        </checkbox-group>
        <p>{{ os | json }}</p>
      </div>
      <div slot="desc">
        <p>设置 <code>vertical</code> 属性可以让选项垂直排列</p>
      </div>
      <x-code lang="html" slot="code">{{ code.vertical }}</x-code>
    </example>
    <example title="半选">
      <div slot="demo">
        <div class="mb10">
          <checkbox
            :value="checkAll"
            :indeterminate="indeterminate"
            @click.prevent.native="onCheckAllClick">全选
          </checkbox>
        </div>
        <checkbox-group v-model="os" @change="onOSChange">
          <checkbox label="iOS"></checkbox>
          <checkbox label="Android"></checkbox>
          <checkbox label="Windows"></checkbox>
        </checkbox-group>
        <p>{{ os | json }}</p>
      </div>
      <div slot="desc">
        <p>设置 <code>indeterminate</code> 属性启用半选功能</p>
      </div>
      <x-code lang="html" slot="code">{{ code.indeterminate }}</x-code>
      <x-code lang="html" slot="code">{{ code.indeterminateScript }}</x-code>
    </example>
    <example title="禁用状态">
      <div slot="demo">
        <checkbox-group v-model="foods">
          <checkbox label="烧鸭饭"></checkbox>
          <checkbox label="鱼香茄子"></checkbox>
          <checkbox label="鱼香茄子" disabled></checkbox>
          <checkbox label="红烧排骨" disabled></checkbox>
        </checkbox-group>
        <p>{{ foods | json }}</p>
      </div>
      <div slot="desc">
        <p>设置 <code>disabled</code> 属性禁用选项</p>
      </div>
      <x-code lang="html" slot="code">{{ code.disabled }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/checkbox'

export default {
  name: 'CheckboxDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      single: false,
      os: ['iOS'],
      foods: ['红烧排骨'],
      indeterminate: true,
      checkAll: false
    }
  },

  methods: {
    onCheckAllClick () {
      if (this.indeterminate) {
        this.checkAll = false
      } else {
        this.checkAll = !this.checkAll
      }
      this.indeterminate = false

      if (this.checkAll) {
        this.os = ['iOS', 'Android', 'Windows']
      } else {
        this.os = []
      }
    },

    onOSChange (data) {
      if (data.length === 3) {
        this.indeterminate = false
        this.checkAll = true
      } else if (data.length > 0) {
        this.indeterminate = true
        this.checkAll = false
      } else {
        this.indeterminate = false
        this.checkAll = false
      }
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



