<template>
  <demo-article>
    <p>选择或输入日期，支持年、月、日期等类型，支持选择范围。</p>
    <example title="基础用法">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="date"
              placeholder="选择日期">
            </date-picker>
          </x-col>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="daterange"
              placeholder="选择日期">
            </date-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置属性 <code>type</code> 为 date> 或 daterange 分别显示选择单日和选择范围类型。</p>
        <p> 设置属性 <code>placement</code> 可以更改选择器出现的方向，支持 12 个方向</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.base" :key="item">{{ item }}</x-code>
    </example>
    <example title="快捷方式">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="date"
              placeholder="选择日期"
              :options="dateOptions">
            </date-picker>
          </x-col>
          <x-col :span="12">
            <date-picker
              style="width: 200px"
              type="daterange"
              placeholder="选择日期"
              :options="daterangeOptions"
              @on-change="onDateRangeChange">
            </date-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置属性 <code>options</code> 对象中的 <code>shortcuts</code> 可以设置快捷选项，详见示例代码。</p>
        <p> 其中 <code>value</code> 为函数，必须返回一个日期，如果是 <code>range</code> 类型，需要返回一个数组。<code>value</code> 接收任何正确的日期格式，比如 2016-12-24 或 12/24/16 都是正确的。</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.shortcut" :key="item">{{ item }}</x-code>
    </example>
    <example title="日期格式">
      <div slot="demo">
        <div class="mb10">
          <date-picker
            style="width: 200px;"
            type="date"
            placeholder="选择日期"
            format="yyyy年MM月dd日"
            :value="date1">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 240px;"
            type="daterange"
            format="yyyy/MM/dd"
            placeholder="选择日期"
            :value="daterange1">
          </date-picker>
        </div>
      </div>
      <div slot="desc">
        <p>设置属性 <code>format</code> 可以改变日期的显示格式。</p>
        <p>注意，<code>format</code> 只是改变显示的格式，并非改变 <code>value</code> 值。</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.format" :key="item">{{ item }}</x-code>
    </example>
    <example title="日期时间选择">
      <div slot="demo">
        <div class="mb10">
          <date-picker
            style="width: 200px;"
            type="datetime"
            placeholder="选择日期和时间">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 200px;"
            type="datetime"
            format="yyyy-MM-dd HH:mm"
            placeholder="选择日期和时间(不含秒)">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 300px;"
            type="datetimerange"
            placeholder="选择日期和时间">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="width: 300px;"
            type="datetimerange"
            format="yyyy-MM-dd HH:mm"
            placeholder="选择日期和时间(不含秒)">
          </date-picker>
        </div>
      </div>
      <div slot="desc">
        <p>设置 <code>type</code> 属性为 datetime 或 datetimerange 可以选择日期和时间。</p>
        <p>配合设置 <code>format</code>属性以格式化结果。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.datetime }}</x-code>
      <x-code lang="html" slot="code">{{ code.datetimerange }}</x-code>
    </example>
    <example title="年和月">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <date-picker
              style="width: 200px;"
              type="year"
              placeholder="选择年">
            </date-picker>
          </x-col>
          <x-col :span="12">
            <date-picker
              style="width: 200px;"
              type="month"
              placeholder="选择月">
            </date-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置 <code>type</code> 属性为 year 或 month 可以只选择年或月</p>
      </div>
      <x-code lang="html" slot="code">{{ code.year }}</x-code>
      <x-code lang="html" slot="code">{{ code.month }}</x-code>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <div class="mb10">
          <date-picker
            style="max-width: 200px"
            type="date"
            size="small"
            placeholder="选择日期">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="max-width: 200px"
            type="date"
            placeholder="选择日期">
          </date-picker>
        </div>
        <div class="mb10">
          <date-picker
            style="max-width: 200px"
            type="date"
            size="large"
            placeholder="选择日期">
          </date-picker>
        </div>
      </div>
      <div slot="desc">
        <p>设置 <code>size</code> 属性可以调整组件的尺寸</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.size" :key="item">{{ item }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/date-picker'

export default {
  name: 'DatePickerDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      date1: '2016-01-01',
      daterange1: ['2016-01-01', '2016-02-15'],
      dateOptions: {
        shortcuts: [{
          text: '今天',
          value () {
            return new Date()
          },
          onClick: () => {
            this.$Message.info('点击了今天')
          }
        }, {
          text: '昨天',
          value () {
            let date = new Date()
            date.setTime(date.getTime() - 24 * 3600 * 1000)
            return date
          },
          onClick: () => {
            this.$Message.info('点击了昨天')
          }
        }, {
          text: '前一周',
          value () {
            let date = new Date()
            date.setTime(date.getTime() - 7 * 24 * 3600 * 1000)
            return date
          },
          onClick: () => {
            this.$Message.info('点击了前一周')
          }
        }]
      },
      daterangeOptions: {
        shortcuts: [{
          text: '最近一周',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 7 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('点击了最近一周')
          }
        }, {
          text: '最近一个月',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 30 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('点击了最近一个月')
          }
        }, {
          text: '最近三个月',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 90 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('点击了最近三个月')
          }
        }]
      }
    }
  },

  methods: {
    onDateRangeChange (range) {
      this.$Message.info(`选中了${range[0]}至${range[1]}`)
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



