let code = {}

code.base = {}
code.base.date = `
<date-picker
  style="width: 200px"
  type="date"
  placeholder="选择日期">
</date-picker>
`
code.base.daterange = `
<date-picker
  style="width: 200px"
  type="daterange"
  placeholder="选择日期">
</date-picker>
`

code.shortcut = {}
code.shortcut.date = `
<date-picker
  style="width: 200px"
  type="date"
  placeholder="选择日期"
  :options="dateOptions">
</date-picker>
`
code.shortcut.daterange = `
<date-picker
  style="width: 200px"
  type="daterange"
  placeholder="选择日期"
  :options="daterangeOptions"
  @on-change="onDateRangeChange">
</date-picker>
`
code.shortcut.script = `
<script>
export default {
  data () {
    return {
      dateOptions: {
        shortcuts: [{
          text: '今天',
          value () {
            return new Date()
          },
          onClick: () => {
            this.$Message.info('点击了今天')
          }
        }, {
          text: '昨天',
          value () {
            let date = new Date()
            date.setTime(date.getTime() - 24 * 3600 * 1000)
            return date
          },
          onClick: () => {
            this.$Message.info('点击了昨天')
          }
        }, {
          text: '前一周',
          value () {
            let date = new Date()
            date.setTime(date.getTime() - 7 * 24 * 3600 * 1000)
            return date
          },
          onClick: () => {
            this.$Message.info('点击了前一周')
          }
        }]
      },
      daterangeOptions: {
        shortcuts: [{
          text: '最近一周',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 7 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('点击了最近一周')
          }
        }, {
          text: '最近一个月',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 30 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('点击了最近一个月')
          }
        }, {
          text: '最近三个月',
          value () {
            let end = new Date()
            let start = new Date()
            start.setTime(start.getTime() - 90 * 24 * 3600 * 1000)
            return [start, end]
          },
          onClick: () => {
            this.$Message.info('点击了最近三个月')
          }
        }]
      }
    }
  }
}
</script>
`

code.format = {}
code.format.date = `
<date-picker
  style="width: 200px;"
  type="date"
  placeholder="选择日期"
  format="yyyy年MM月dd日"
  :value="date1">
</date-picker>
`
code.format.daterange = `
<date-picker
  style="width: 240px;"
  type="daterange"
  format="yyyy/MM/dd"
  placeholder="选择日期"
  :value="daterange1">
</date-picker>
`

code.datetime = `
<date-picker
  style="width: 200px;"
  type="datetime"
  placeholder="选择日期和时间">
</date-picker>
`
code.datetimerange = `
<date-picker
  style="width: 300px;"
  type="datetimerange"
  placeholder="选择日期和时间">
</date-picker>
`

code.year = `
<date-picker
  style="width: 200px;"
  type="year"
  placeholder="选择年">
</date-picker>
`
code.month = `
<date-picker
  style="width: 200px;"
  type="month"
  placeholder="选择月">
</date-picker>
`

code.size = {}
code.size.small = `
<date-picker
  style="max-width: 200px"
  type="date"
  size="small"
  placeholder="选择日期">
</date-picker>
`
code.size.large = `
<date-picker
  style="max-width: 200px"
  type="date"
  size="large"
  placeholder="选择日期">
</date-picker>
`

export default code



