let code = {}

code.base = `
<validator name="validatorNameXXX">
  <input
    type="text"
    placeholder="请输入手机号码"
    name="phone"
    v-model="val1"
    :rules="{required: true, phone: true}"
    >
  <!-- 上面是比较简单的用法, 使用默认提示文案,
  如果要自定义提示文案, 可以像下面的例子这样传rules参数 -->
  <x-input
    v-model="val2"
    name="phone"
    :rules="{
      required: {
        value: true,
        message: '自定义的必填提示'
      },
      maxlength: {
        value: 5,
        message: '你妈说你长度超过{value}了'
      }
    }"
    :delay="2000"
    >
  </x-input>
</validator>
`

code.js = `
// 验证表单validatorName的phone表单元素
this.$Validator['validatorNameXXX'].validate('phone')

// 验证表单validatorNameXXX的全部表单元素(不指定某个表单元素则表示验证整个表单)
this.$Validator['validatorNameXXX'].validate()

// 重置表单validatorNameXXX
this.$Validator['validatorNameXXX'].reset()

// 查看表单validatorNameXXX信息
// 可以查看到每个表单当前的状态和每个验证条件的验证情况
console.log(this.$Validator['validatorNameXXX'].formItems)

// 当前表单是否有错
console.log(this.$Validator['validatorNameXXX'].isError)
// 当前表单的所有错误信息
console.log(this.$Validator['validatorNameXXX'].errors)


`

export default code



