<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <scroll-view class="scroll-view" >
          <div class="scroll-content"></div>
        </scroll-view>
      </div>
      <div slot="desc">
        <p>如果不需要监听函数, 不需要自定义容器, 那么使用右侧第一个示例代码即可</p>
        <p>右侧第二部分代码是详细所有可以自定义的属性和回调函数使用示例, 具体每个属性和每个回调函数的详细信息可以打开组件, 组件里面备注了每个属性和回调函数的信息</p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
      <x-code lang="html" slot="code">{{ code.details }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/scroll-view'

export default {
  name: 'ScrollBarDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.scroll-view
  width 100%
  height 200px
  .scroll-content
    width 1000px
    height 400px
    background linear-gradient(0, red, rgba(0, 0, 255, 0.5))
</style>



