let code = {}

code.base = `
<x-switch v-model="s1" @change="$Message.info('当前变量值为' + s1)"></x-switch>
`

code.size = `
<x-switch size="small"></x-switch>
`

code.width = `
<x-switch :width="60"><span slot="on">ON</span><span slot="off">OFF</span></x-switch>
`

code.type = `
<x-switch type="success" :value="true"></x-switch>
`

code.disabled = `
<x-switch disabled></x-switch>
`

export default code



