<template>
  <demo-article>
    <p>静态地呈现一些警告信息，可手动关闭</p>
    <example title="基础用法">
      <div slot="demo">
        <alert>消息提示文案</alert>
        <alert type="success">消息提示文案</alert>
        <alert type="warning">消息提示文案</alert>
        <alert type="error">消息提示文案</alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="含描述">
      <div slot="demo">
        <alert>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="success">
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="warning">
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="error">
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.desc }}</x-code>
    </example>
    <example title="含图标">
      <div slot="demo">
        <p>不含描述</p>
        <alert show-icon>消息提示文案</alert>
        <alert type="success" show-icon>消息提示文案</alert>
        <alert type="warning" show-icon>消息提示文案</alert>
        <alert type="error" show-icon>消息提示文案</alert>
        <p>含描述</p>
        <alert show-icon>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="success" show-icon>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="warning" show-icon>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="error" show-icon>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="大尺寸">
      <div slot="demo">
        <p>只含描述</p>
        <alert show-icon size="large">
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <p>含标题和描述</p>
        <alert show-icon size="large">
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
      </div>
      <div slot="desc">
      </div>
      <x-code lang="html" slot="code">{{ code.icon }}</x-code>
    </example>
    <example title="可关闭">
      <div slot="demo">
        <alert closable>消息提示文案</alert>
        <alert show-icon closable>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
        <alert type="warning" closable>
          消息提示文案
          <template slot="close"><span class="hl-info">不再提示</span></template>
        </alert>
      </div>
      <div slot="desc">
        <p>设置 <code>closable</code> 属性给组件增加关闭功能</p>
      </div>
      <x-code lang="html" slot="code">{{ code.closable }}</x-code>
    </example>
    <example title="无边框背景">
      <div slot="demo">
        <alert show-icon naked>
          消息提示文案
          <template slot="desc">提示描述文案提示描述文案提示描述文案</template>
        </alert>
      </div>
      <div slot="desc">
        <p>设置 <code>naked</code> 属性隐藏边框和背景</p>
      </div>
      <x-code lang="html" slot="code">{{ code.naked }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/alert'

export default {
  name: 'AlertDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



