let code = {}

code.drag = `
<draggable
  :data="'希望drop组件接收到的数据'"
  @dragstart="onSourceDragStart"
  @dragsend="onSourceDragEnd">

  <div class="drag-content">拖动我</div>

</draggable>
`

code.drop = `
<droppable
  class="drop-box"
  @dragstart="onDragStart"
  @dragend="onDragEnd"
  @drop="onDrop"
  @dragenter="onDragEnter"
  @dragover="onDragOver"
  @dragleave="onDragLeave">

  <div>被拖动的东西可以在这里放下</div>

</droppable>
`

export default code



