let code = {}

code.info = `
// 弹出确认框(info)
this.$Confirm.info('可以只传内容, 也可以传一个完整的对象')
`
code.warning = `
// 弹出确认框(warning)
this.$Confirm.warning('可以只传内容, 也可以传一个完整的对象')
`
code.error = `
// 弹出确认框(error)
this.$Confirm.error('可以只传内容, 也可以传一个完整的对象')
`
code.success = `
// 弹出确认框(success)
this.$Confirm.success('可以只传内容, 也可以传一个完整的对象')
`

code.base = `
// 通用写法
this.$Confirm.show({
  title: '测试的标题',
  content: '测试的内容',
  type: 'warning'
})
`

code.promise = `
// promise写法
this.$Confirm.warning('测试的内容').then((val) => {
  val ? console.log('点了确定') : console.log('点了取消')
}).catch((err) => {
  console.log(err)
})
`

code.async = `
// async函数写法
async showConfitm () {
  if (await this.$Confirm.warning('测试的内容')) {
    console.log('确定后的逻辑')
  } else {
    console.log('取消后的逻辑')
  }
}
`

export default code



