let code = {}

code.single = `
<checkbox v-model="single">今年发大财</checkbox>
`

code.group = `
<checkbox-group v-model="os" @change="onOSChange">
  <checkbox label="iOS"></checkbox>
  <checkbox label="Android"></checkbox>
  <checkbox label="Windows"></checkbox>
</checkbox-group>
`

code.vertical = `
<checkbox-group v-model="os" @change="onOSChange" vertical>
  <checkbox label="iOS"></checkbox>
  <checkbox label="Android"></checkbox>
  <checkbox label="Windows"></checkbox>
</checkbox-group>
`

code.indeterminate = `
<div class="mb10">
  <checkbox
    :value="checkAll"
    :indeterminate="indeterminate"
    @click.prevent.native="onCheckAllClick">全选
  </checkbox>
</div>
<checkbox-group v-model="os" @change="onOSChange">
  <checkbox label="iOS"></checkbox>
  <checkbox label="Android"></checkbox>
  <checkbox label="Windows"></checkbox>
</checkbox-group>
`

code.indeterminateScript = `
<script>
export default {
  data () {
    return {
      os: ['iOS'],
      indeterminate: true,
      checkAll: false
    }
  },

  methods: {
    onCheckAllClick () {
      if (this.indeterminate) {
        this.checkAll = false
      } else {
        this.checkAll = !this.checkAll
      }
      this.indeterminate = false

      if (this.checkAll) {
        this.os = ['iOS', 'Android', 'Windows']
      } else {
        this.os = []
      }
    },

    onOSChange (data) {
      if (data.length === 3) {
        this.indeterminate = false
        this.checkAll = true
      } else if (data.length > 0) {
        this.indeterminate = true
        this.checkAll = false
      } else {
        this.indeterminate = false
        this.checkAll = false
      }
    }
  }
}
</script>
`

code.disabled = `
<checkbox-group v-model="foods">
  <checkbox label="烧鸭饭"></checkbox>
  <checkbox label="鱼香茄子"></checkbox>
  <checkbox label="红烧排骨" disabled></checkbox>
</checkbox-group>
`

export default code



