<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <pagination
          :total="100"
          :current="1">
        </pagination>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
    <example title="基础用法">
      <div slot="demo">
        <pagination
          :total="100"
          :current="1"
          simple>
        </pagination>
      </div>
      <div slot="desc">
        <p>指定 <code>simple</code> 属性启用简单分页</p>
      </div>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'

export default {
  name: 'PaginationDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



