<template>
  <demo-article>
    <p>选择或输入标准时间，支持选择范围</p>
    <example title="基础用法">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <time-picker
              style="width: 168px;"
              type="time"
              placeholder="选择时间">
            </time-picker>
          </x-col>
          <x-col :span="12">
            <time-picker
              style="width: 200px;"
              type="timerange"
              placeholder="选择时间">
            </time-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置属性 <code>type</code> 为 time 或 timerange 分别显示选择单日和选择范围类型。</p>
        <p> 设置属性 <code>placement</code> 可以更改选择器出现的方向，支持 12 个方向</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.base" :key="item">{{ item }}</x-code>
    </example>
    <example title="时间格式">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <time-picker
              style="width: 168px;"
              type="time"
              placeholder="选择时间"
              format="HH点mm分ss秒"
              :value="time1">
            </time-picker>
          </x-col>
          <x-col :span="12">
            <time-picker
              style="width: 200px;"
              type="timerange"
              placeholder="选择时间"
              format="HH:mm:ss"
              :value="timerange1">
            </time-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置属性 <code>format</code> 可以改变时间的显示格式。</p>
        <p>注意，<code>format</code> 只是改变显示的格式，并非改变 <code>value</code> 值。</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.format" :key="item">{{ item }}</x-code>
    </example>
    <example title="选择时分">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <time-picker
              style="width: 168px;"
              type="time"
              format="HH:mm"
              placeholder="选择时间">
            </time-picker>
          </x-col>
          <x-col :span="12">
            <time-picker
              style="width: 200px;"
              type="timerange"
              format="HH:mm"
              placeholder="选择时间"
              placement="bottom-end">
            </time-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>下拉选项会根据 <code>format</code> 的值自动隐藏不需要设置的列</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.noSecond" :key="item">{{ item }}</x-code>
    </example>
    <example title="不可选时间">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <time-picker
              style="width: 168px;"
              type="time"
              placeholder="选择时间"
              :disabled-hours="[1,5,10]"
              :disabled-minutes="[0,10,20]">
            </time-picker>
          </x-col>
          <x-col :span="12">
            <time-picker
              style="width: 240x;"
              type="timerange"
              placeholder="选择时间"
              :disabled-hours="[1,5,10]"
              :disabled-minutes="[0,10,20]">
            </time-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置 <code>disabled-hours</code>、<code>disabled-minutes</code> 或 <code>disabled-seconds</code> 属性可以让某些选项处于不可选状态</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.disabled" :key="item">{{ item }}</x-code>
    </example>
    <example title="带有确认操作">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <time-picker
              style="width: 168px;"
              type="time"
              confirm
              placeholder="选择时间">
            </time-picker>
          </x-col>
          <x-col :span="12">
            <time-picker
              style="width: 200px;"
              type="timerange"
              confirm
              placeholder="选择时间">
            </time-picker>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置属性 <code>confirm</code>，选择器会有清空和确定按钮。</p>
        <p>确认按钮并没有影响时间的正常选择。</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.confirm" :key="item">{{ item }}</x-code>
    </example>
    <example title="尺寸">
      <div slot="demo">
        <div class="mb10">
          <time-picker
            style="width: 120px;"
            type="time"
            size="small"
            placeholder="选择时间">
          </time-picker>
        </div>
        <div class="mb10">
          <time-picker
            style="width: 120px;"
            type="time"
            placeholder="选择时间">
          </time-picker>
        </div>
        <div class="mb10">
          <time-picker
            style="width: 120px;"
            type="time"
            size="large"
            placeholder="选择时间">
          </time-picker>
        </div>
      </div>
      <div slot="desc">
        <p>设置 <code>size</code> 属性可以调整组件的尺寸</p>
      </div>
      <x-code lang="html" slot="code" v-for="item in code.size" :key="item">{{ item }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/time-picker'

export default {
  name: 'TimePickerDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      time1: '09:41:00',
      timerange1: ['09:41:00', '12:00:00']
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



