<template>
  <article class="demo-article">
    <h1><span>{{ title }}</span></h1>
    <slot></slot>
  </article>
</template>

<script>
import nav from '../data/nav'

export default {
  name: 'DemoArticle',

  data () {
    return {
      title: ''
    }
  },

  mounted () {
    let path = this.$route.path.replace(/\/component\//, '')

    nav.forEach(item => {
      if (item.links.hasOwnProperty(path)) {
        this.title = item.links[path]
        return
      }
    })
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.demo-article
  /*overflow-x hidden
  overflow-y auto*/
  padding 20px 30px

  h1
    position relative
    font-weight normal
    margin 0 0 20px

    &:before
      absolute left top 50%
      content ''
      size 100% 0
      border-top-width 1px
      border-top-style solid

    span
      position relative
      display inline-block
      padding-right 20px
      font-size 24px

  .sub-title
    font-size 18px
    font-weight normal
    margin 30px 0

// 主题配色
.theme-light
  .demo-article
    h1
      &:before
        border-top-color #E3E3E3
      span
        background-color #FFF
    .sub-title
      color gray
.theme-dark
  .demo-article
    h1
      &:before
        border-top-color #323238
      span
        background-color #141414
    .sub-title
      color #666
</style>



