<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <dropdown>
              <x-button>菜单 <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="12">
            <dropdown trigger="custom" :visible="visible">
              <x-button @click.native="visible=true">自定义内容 <icon type="angle-down"></icon></x-button>
              <div slot="content" class="tac" style="width: 200px; height: 80px;">
                <p>这里可以是任意的自定义内容</p>
                <x-button type="primary" size="small" @click.native="visible=false">关闭</x-button>
              </div>
            </dropdown>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
    <example title="触发方式">
      <div slot="demo">
        <x-row>
          <x-col :span="12">
            <dropdown>
              <x-button>滑过触发 <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="12" trigger="click">
            <dropdown trigger="click">
              <x-button>点击触发 <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.trigger }}</x-code>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
    <example title="对齐方向">
      <div slot="demo">
        <x-row>
          <x-col :span="8">
            <dropdown placement="bottom-start">
              <x-button>居左 <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="8">
            <dropdown>
              <x-button>居中 <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
          <x-col :span="8">
            <dropdown placement="bottom-end">
              <x-button>居右 <icon type="angle-down"></icon></x-button>
              <dropdown-menu slot="content">
                <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
              </dropdown-menu>
            </dropdown>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.placement }}</x-code>
    </example>
    <example title="菜单项点击">
      <div slot="demo">
        <dropdown @item-click="onItemClick">
          <x-button>菜单 <icon type="angle-down"></icon></x-button>
          <dropdown-menu slot="content">
            <dropdown-item v-for="item in menu" :key="item.name" :name="item.name">{{ item.label }}</dropdown-item>
          </dropdown-menu>
        </dropdown>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.item }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/dropdown'
import find from 'lodash/find'

export default {
  name: 'DropdownDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      visible: false,
      menu: [{
        name: 'link',
        label: '连接平台'
      }, {
        name: 'dev',
        label: '开发平台'
      }, {
        name: 'operation',
        label: '运营平台'
      }, {
        name: 'data',
        label: '数据平台'
      }]
    }
  },

  methods: {
    /**
     * 处理菜单项点击
     * @param  {String} name 菜单项标识
     */
    onItemClick (name) {
      let label = find(this.menu, { name }).label
      this.$Message.info(`点击了${label}`)
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



