<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <x-slider :value="value" :disabled="disabled" @change="val => value = val"></x-slider>
        <p>
          disabled:
          <x-switch v-model="disabled" @change=""></x-switch>
        </p>
        <x-button @click.native="value = value + 1">+1</x-button>
        <x-button @click.native="value = value - 1">-1</x-button>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'

export default {
  name: 'SliderDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      disabled: false,
      value: 80
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



