<template>
  <demo-article>
    <p>包括行组件<code>XRow</code>和列组件<code>XCol</code>，对它们进行合理组合可以实现栅格化布局。</p>
    <example title="基础用法">
      <div slot="demo">
        <x-row class="mb20">
          <x-col :span="24">
            <div class="content-container">24列</div>
          </x-col>
        </x-row>
        <x-row class="mb20">
          <x-col :span="12">
            <div class="content-container">12列</div>
          </x-col>
          <x-col :span="12">
            <div class="content-container">12列</div>
          </x-col>
        </x-row>
        <x-row class="mb20">
          <x-col :span="8">
            <div class="content-container">8列</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8列</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8列</div>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>水平排列的布局。<code>&lt;x-col&gt;</code> 必须放在 <code>&lt;x-row&gt;</code> 里面。</p>
        <p>容器等分为24列，指定 <code>x-col</code> 的 <code>span</code> 属性以设置所占列数。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.base }}</x-code>
    </example>
    <example title="栅格间距">
      <div slot="demo">
        <x-row class="mb20" :gutter="10">
          <x-col :span="8">
            <div class="content-container">8列</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8列</div>
          </x-col>
          <x-col :span="8">
            <div class="content-container">8列</div>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置<code>&lt;x-row&gt;</code>的<code>gutter</code> 属性定义栅格之间的间距。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.gutter }}</x-code>
    </example>
    <example title="栅格偏移">
      <div slot="demo">
        <x-row class="mb20">
          <x-col :span="8" :offset="4">
            <div class="content-container">8列</div>
          </x-col>
        </x-row>
        <x-row>
          <x-col :span="16" :push="8">
            <div class="content-container">16列</div>
          </x-col>
          <x-col :span="8" :pull="16">
            <div class="content-container">8列</div>
          </x-col>
        </x-row>
      </div>
      <div slot="desc">
        <p>设置<code>&lt;x-col&gt;</code>的<code>offset</code>、<code>push</code> 或 <code>pull</code> 属性定义栅格的偏移。</p>
      </div>
      <x-code lang="html" slot="code">{{ code.offset }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/grid'

export default {
  name: 'GridDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/styles/common'

.example-demo
  [class^='x-col']
    .content-container
      background-color rgba(extra-light-gray, 30%)
      line-height 30px
      text-align center
      color light-gray

    &:nth-child(odd)
      .content-container
        background-color rgba(extra-light-gray, 50%)
</style>



