let code = {}

code.start = `
// 弹出加载图标(start)
let option = { // 可选参数，可不传
  text: 'global loading',
  withMask: true,
  color: 'red',
  size: 50
}
this.$Spin.start(option)
`
code.stop = `
// 关闭加载图标(stop)
this.$Spin.stop()
`

code.local = `
<spin
  :show="true"
  text="loading"
  :with-mask="true"
  color="red"
  :size="50">
</spin>
`

export default code



