<template>
  <demo-article>
    <p>位于页面最顶部的加载进度条</p>
    <example title="基础用法">
      <div slot="demo">
        <x-button @click.native="showLoading">开始loading</x-button>
        <x-button @click.native="doneLoading">完成</x-button>
      </div>
      <div slot="desc">
        <p>全局方法 this.$LoadingBar.run(percent)</p>
        <p>方法接受的参数可以是一个对象 也可以是一个数字 如果是一个数字则表示进度条的百分比</p>
        <p>当进度条走到百分之百之后就会自动消失</p>
      </div>
      <x-code lang="javascript" slot="code">{{ code.base }}</x-code>
      <x-code lang="javascript" slot="code">{{ code.complete }}</x-code>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/loading-bar'

export default {
  name: 'LoadingBarDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code
    }
  },

  mounted () {
  },

  methods: {
    showLoading () {
      this.$LoadingBar.run({
        color: 'yellow',
        percent: 30,
        duration: 1000
      })

      setTimeout(() => {
        this.$LoadingBar.run({
          color: 'red',
          percent: 60,
          duration: 3000
        })
      }, 1500)

      setTimeout(() => {
        this.$LoadingBar.run(80)
      }, 5000)
    },

    doneLoading () {
      this.$LoadingBar.run({
        color: 'red',
        percent: 100,
        duration: 3000
      })
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



