<template>
  <demo-article>
    <p>这里是组件简介。</p>
    <example title="基础用法">
      <div slot="demo">
        <tabs :nav="nav">
          <router-link :to="item.link" v-for="item in nav" tag="li" :key="item">{{ item.label }}</router-link>
        </tabs>
      </div>
      <div slot="desc">
        <p>这里是用法描述。</p>
      </div>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
    <example title="基础用法">
      <div slot="demo">
        <tabs :nav="nav" type="line">
          <router-link :to="item.link" v-for="item in nav" tag="li" :key="item">{{ item.label }}</router-link>
        </tabs>
      </div>
      <div slot="desc">
        <p>指定 <code>type</code> 属性可以更换风格，可选值为 <code>card</code> 和 <code>line</code>，默认值为 <code>card</code></p>
      </div>
      <div slot="code">
        <p>这里是代码样例。</p>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'

export default {
  name: 'TabsDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      nav: [{
        link: '/component/tabs',
        label: '设备管理'
      }, {
        link: 'users',
        label: '用户管理'
      }, {
        link: 'history',
        label: '历史数据'
      }]
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



