<template>
  <demo-article>
    <p>表单验证组件, 同一个页面可以出现多个</p>
    <example title="基础用法">
      <div slot="demo">
        <validator name="validatorName">
          <span>别的位置的表单</span>
          <input class="mb30" type="text" placeholder="长度超过2就会报错" name="test"  v-model="val" :rules="{maxlength: 2}">
          <div class="aaaaa">
            <span>账户</span>
            <x-input
              class="mb30"
              placeholder="请输入账号"
              v-model="val1"
              name="name"
              :rules="{ required: {value: true, message: '自定义的必填提示'}, phone: true }"
              delay="1000"
              >
            </x-input>
            <span>密码</span>
            <x-input
              class="mb30"
              placeholder="请输入密码"
              v-model.lazy="val2"
              name="password"
              :rules="{required: true}"
              >
            </x-input>
          </div>
        </validator>
        <x-button @click.native="checkItem('password')">验证密码</x-button>
        <x-button @click.native="checkAll">验证全部表单</x-button>
        <x-button @click.native="reset">重置</x-button>
        <x-button @click.native="logValidator">打印当前表单验证信息</x-button>
      </div>
      <div slot="desc">
        <p>如右边代码所示, 使用的时候每个表单元素的name属性都是必填项, 每个表单的rules属性也是必填项, 带有rules属性的不一定是默认的表单元素, 也可以是普通div, 只要有value属性和name属性即可</p>
        <p>rules属性可以直接传一个带有验证字段属性的对象, 如: {require: true, max: 1000}, 这时候会用默认的错误提示</p>
        <p>如果需要自定义错误提示文案, 可以这样传rules属性, 如: {max: {value: 1000, message: '最大不能超过{value}'}}, 组件会自动将{value}替换为当前rules对象中对应属性的值</p>
        <p>组件自带错误提示样式, 如果不想用默认的错误提示样式, 可以将validator的:use-default-style设置为false</p>
        <p>组件可以自定义验证延时时间, 默认500(毫秒), 可以通过可以将validator的属性:delay自定义更改, 所有表单元素如果没有单独设置都会使用这个时间</p>
        <p>每个表单也可以单独设置实时验证延时, 默认使用validator上的delay时间, 也可以单独设置某一项的延时时间和别的不一样</p>
        <p>如果不希望实时验证, 希望等到光标离开的时候才验证, 可以讲改项表单元素的延时时间设置成100000或者更长</p>
        <p>暂时不支持异步验证, 后期再实现异步验证</p>
        <br>
        <p>属性解释:</p>
        <pre>
          // 内置验证器: (rules可以使用的属性)
          {
            required: 是否必填 (Boolean),
            max: 最大值 (Number),
            min: 最小值 (Number),
            maxlength: 最大长度 (Number),
            minlength: 最小长度 (Number),
            number: 是否必须是数字 (Boolean),
            integer: 是否必须是整数 (Boolean),
            phone: 是否是手机号码 (Boolean),
            email: 是否是邮箱 (Boolean),
            url: 是否是url (Boolean),
            regexp: 自定义正则表达式 (RegExp),
            method: 自定义验证函数 (Function)
          }
          // 对外暴露的表单信息对象(this.$Validator.XXX)
          {
            formItems: [
              value: 当前表单的value值
              name: 当前表单定义的name属性
              rules: 当前表单的规则
              delay: 验证延时时间
              messages: 当前表单每个规则不通过的提示信息
              elm: 当前表单的DOM节点
              vNode: 当前表单的虚拟节点
              isComponent: 当前表单是否为组件
              dirty: 当前表单是否脏 (是否已经经过用户操作, 判断依据是光标是否进入过)
              results: 当前表单的验证结果
              errors: 当前表单验证不通过的所有信息
              isRight: 当前表单是否全部验证通过
              isError: 当前表单是否验证不通过
              isShowError: 是否显示错误提示
            ]
            isError: 布尔值 验证是否全部通过(验证不通过不一定有错误提示, 有错误提示表示验证肯定不通过)
            isShowError: 布尔值 是否存在错误提示(验证不通过不一定有错误提示, 有错误提示表示验证肯定不通过)
            reset: 静态方法 重置表单(去掉所有错误提示)
            validate: 静态方法 验证某一项或者验证全部
          }
        </pre>
      </div>
      <div slot="code">
        <x-code lang="html" slot="code">{{ code.base }}</x-code>
        <x-code lang="javascript" slot="code">{{ code.js }}</x-code>
      </div>
    </example>
  </demo-article>
</template>

<script>
import DemoArticle from './components/demo-article'
import Example from './components/example'
import XCode from './components/code'
import code from './code/validator'

export default {
  name: 'ValidatorDoc',

  components: {
    DemoArticle,
    Example,
    XCode
  },

  data () {
    return {
      code,
      val: '',
      val1: '',
      val2: ''
    }
  },

  methods: {
    /**
     * 验证某一项表单元素
     */
    checkItem (formItemName) {
      // 指定表单元素名字表示验证某一个表单
      // 不指定名字表示验证所有表单
      this.$Validator['validatorName'].validate(formItemName)
    },
    /**
     * 验证全部表单
     */
    checkAll () {
      // 指定表单元素名字表示验证某一个表单
      // 不指定名字表示验证所有表单
      this.$Validator['validatorName'].validate()
    },

    /**
     * 重置验证
     */
    reset () {
      this.$Validator['validatorName'].reset()
    },

    /**
     * 打印验证信息
     */
    logValidator () {
      console.log(this.$Validator['validatorName'])
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'
</style>



